/* montserrat-regular - cyrillic-ext */
@import "variables.scss";

@font-face {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  src: local('Montserrat Regular'), local('Montserrat-Regular'),
       url('../fonts/montserrat-v14-cyrillic-ext-regular.woff2') format('woff2'),
       url('../fonts/montserrat-v14-cyrillic-ext-regular.woff') format('woff');
}
/* montserrat-italic - cyrillic-ext */

@font-face {
  font-family: 'Montserrat';
  font-style: italic;
  font-weight: 400;
  src: local('Montserrat Italic'), local('Montserrat-Italic'),
       url('../fonts/montserrat-v14-cyrillic-ext-italic.woff2') format('woff2'),
       url('../fonts/montserrat-v14-cyrillic-ext-italic.woff') format('woff');
}
