body {
  min-width: min-content;
  width: 100%;

  box-sizing: border-box;
  
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: normal;
	font-size: 12px;

  color: rgba(31, 32, 65, 0.75);
}

h1 {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 30px;

  color: #1F2041;
}

h2 {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 24px;

  color: #1F2041;
}

h3 {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  /* identical to box height */
  
  text-transform: uppercase;
  
  color: #1F2041;
}

input {
  border: none;
}

.input {
  border: 1px solid rgba(31, 32, 65, 0.25);
  box-sizing: border-box;
  border-radius: 4px;
}

main {
  background: #FFFFFF;
  box-sizing: border-box;
  padding-bottom: 20px;
}

.content_wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

	max-width: $content_width;
  margin-left: auto;
  margin-right: auto;
  
  box-sizing: border-box;
}



content {
  flex-grow: 1;
  box-sizing: border-box;
}

.__preview {
  width: 100%;
  padding-bottom: $header_height;
}

[data-title] {
  position: relative;
}

[data-title]::after {
  position: absolute;

  min-width: 300px;
  z-index: 1;
  right: 0%;

  background: rgba(255,255,255,0.9);
  content: attr(data-title);
  opacity: 0;

  pointer-events: none;

  padding: 20px;

  border: 1px solid rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;

  transition: 0.5s;
}

[data-title]:hover::after {
  opacity: 1;
}

#subscribe > div {
  display: flex;
  flex-wrap: nowrap;
}

#subscribe a {
  display: flex;
  margin-left: auto;
  margin-right: 5px;

  font-family: 'Material Icons';
  font-size: 24px;

}

#subscribe input {
  margin-left: 10px;

  font-family: inherit;
  font-style: inherit;
  font-weight: inherit;
}
